import React, {Component} from "react"
import {connect} from "react-redux"
import {saveSelectedVoipNumber, selectVoipNumber, showVoipNumbersModal, voipSelection} from "../actions/voip"
import OraModal from "../../core/components/ui/Modal"
import OraRadioButton from "./OraRadioButtonGroup";
import CmsDescription from "../../core/components/ui/CmsDescription";
import * as FormatUtils from "../../../utils/FormatUtils";
import {getSelectedVoipNumber, getShowVoipNumbersModal, getVoipNumbers} from "../selectors/voip";

class VoipNumbersComponent extends Component {

    constructor(props){
        super(props);
        this.onAccept = this.onAccept.bind(this);
        this.state = {
            selectedNumber: this.props.selectedVoipNumber
        }
    }

    componentWillReceiveProps(nextProps) {
        if (this.state.selectedNumber !== nextProps.selectedVoipNumber) {
            this.setState({selectedNumber: nextProps.selectedVoipNumber});
        }
    }

    onAccept(e) {
        e.preventDefault();
        this.props.selectVoipNumber(this.state.selectedNumber);
        this.props.saveSelectedVoipNumber(this.state.selectedNumber);
        //TODO: onClose x2?
        this.onClose();
    }

    onClose() {
        this.props.stopVoipSelection();
        this.props.closeModal();
    }

    onValueChange({value}) {
        this.setState({selectedNumber: value});
    }

    formatNumber(number) {
        return FormatUtils.formatNumber(number, '## ### ## ##');
    }

    render() {
        return(
                    <OraModal onClose={() => this.onClose()} open={this.props.showModal} id={"voipNumbersModal"} showClose={true} escapeClose={true} clickClose={true} size="narrow">
                        <div>
                            <div className="row">
                                <div className="l-col l-col-small-12 l-col-medium-12 l-col-12 ">
                                    <CmsDescription description={this.props.descriptions['secondTitle']}/>
                                </div>
                            </div>

                            <div className="row">
                                <div className="l-col l-col-small-12 l-col-medium-12 l-col-12 ">
                                    <CmsDescription description={this.props.descriptions['yourNewNumber']}/>
                                </div>
                            </div>

                            <div className="row">
                                <div className="l-col l-col-small-12 l-col-medium-12 l-col-12 ">
                                    <p className="h2 u-spacing-xl"> {this.formatNumber(this.state.selectedNumber)} </p>
                                </div>
                            </div>

                            <div className="row">
                                <div className="l-col l-col-small-12 l-col-medium-12 l-col-12 ">
                                    <fieldset className="opl-radiogroup opl-inputgroup opl-inputgroup--vertical g-gray2-bg u-padding-m u-padding-top-m u-padding-left-m">
                                        {
                                            this.props.voipNumbers.map(item =>
                                                <OraRadioButton  name="selectedNumber" text={this.formatNumber(item)} value={item} onValueChange={this.onValueChange.bind(this)}
                                                                 selectedValue={this.state.selectedNumber}/>)
                                        }
                                    </fieldset>
                                </div>
                            </div>

                            <div className="row">
                                <div className="l-col l-col-small-12 l-col-medium-12 l-col-12 u-padding-l u-padding-top-l">
                                    <button className="o-btn opl-btn opl-btn--primary u-w-100" onClick={this.onAccept}>Dalej</button>
                                </div>
                            </div>
                        </div>
                    </OraModal>
        )
    }

}

const mapStateToProps = state => ({
    showModal: getShowVoipNumbersModal(state),
    voipNumbers: getVoipNumbers(state),
    selectedVoipNumber: getSelectedVoipNumber(state)
});

const mapDispatchToProps = dispatch => ({
    closeModal: () => dispatch(showVoipNumbersModal(false)),
    selectVoipNumber: (number) => dispatch(selectVoipNumber(number)),
    saveSelectedVoipNumber: (number) => dispatch(saveSelectedVoipNumber(number)),
    stopVoipSelection: () => dispatch(voipSelection(false))
});

export default connect(mapStateToProps, mapDispatchToProps)(VoipNumbersComponent);